//轮询半段是否已经将列表加载出来，加载完成之后执行方法
refreshPage();

async function refreshPage() {
    let target = document.getElementsByClassName('_2QgSC')[0]
    let  zbaseConfig1 = await chrome.storage.local.get(['zbaseConfig'])
    if (target && zbaseConfig1.zbaseConfig && zbaseConfig1.zbaseConfig.data.config) {
        const style = document.createElement('style');
        style.type = 'text/css'
        style.rel = 'stylesheet';
        document.head.appendChild(style)
        style.appendChild(document.createTextNode('#wow-community-add-button,#wow-group-add-button{position: relative;z-index: 33; background: rgb(8 196 29);color: white;padding: 5px 7px;font-size: 14px;border-radius: 5px;cursor: pointer; display: flex;justify-content: center; align-items: center; }'))
        // style.appendChild(document.createTextNode('#wow-icon-box{width: 20px; height: 20px; margin-right: 10px; margin-top: 2px;}'));
        zbaseConfig1.zbaseConfig.data.config.forEach(item => {
            if (item.name === 'Drainage configuration' && item.params && item.params.wowLink === '1') {
                // searchIconAdd(item.params.wowClass);
                groupButtonAdd();
            }
        })
    } else {
        setTimeout(() => {
            refreshPage()
        }, 2000);
    }

}

