function groupButtonAdd() {
    let body = document.getElementsByTagName('body')[0];
    body.addEventListener('click', function () {
        setTimeout(function () {
            chrome.storage.local.get(['wow_group_add_button_noshow'], function (res) {
                if (!res.wow_group_add_button_noshow && !document.getElementById('wow-group-add-button')) {
                    let createDom = document.getElementsByClassName('_1Yy4I')[0];
                    let moreIcon = '';
                    if (createDom) {
                        moreIcon = createDom.getElementsByClassName('_2zjLK')[0];
                    }
                    if (createDom && !moreIcon) {
                        let aDome = document.createElement('p');
                        let icon = document.createElement('img');
                        icon.src = chrome.runtime.getURL('icons/wowLogo.png');
                        icon.style.width = "20px";
                        aDome.appendChild(icon);
                        aDome.innerHTML += "WA Group Sender  <span  style='padding: 0 2px 0 10px; font-size: 13px;'>X</span>";
                        aDome.id = "wow-group-add-button"
                        createDom.appendChild(aDome);
                        let text = createDom.getElementsByClassName('_1FrBz')[0].getElementsByTagName('h1')[0].innerHTML;
                        aDome.onclick = function () {
                            window.open(`https://chrome.google.com/webstore/detail/wa-group-sender/kilbeicibedchlamahiimkjeilnkgmeo?utm_source=privacy_wow_buttonInstall_${text}`);
                        };
                        document.getElementById('wow-group-add-button').getElementsByTagName('span')[0].onclick = function () {
                            createDom.removeChild(aDome);
                            chrome.storage.local.set({wow_group_add_button_noshow: true})
                            if (window.event) {
                                window.event.cancelBubble = true;
                            } else {
                                e.preventDefault();
                            }
                        }
                    }
                }
            })
        }, 500)
    });
}




